
.check-back{
	width: 40px;
	height: 40px;
	background-image: url('../assets/imgs/back-white.png');
	background-size: 40px 40px;
	background-position: center;
	background-repeat: no-repeat;
	display: inline-block;
	cursor: pointer;
	position:absolute;
	top:0px;
	left:15px;
	margin-top: 10px;
	z-index: 1;
}
.show-hide-icon{
	display: inline-block;
	width: 14px;
	height: 10px;
}
.show-icon{
	background-image: url(../common/img/calc/calc-show.png);
}
.hide-icon{
	background-image: url(../common/img/calc/calc-hide.png);
}
.calendar-icon{
	position: absolute;
    top: 38px;
    right:70px;
}
.calc-save-eidt{
	border:none;
	background-color: #B6B6B6;
	color:#fff;
	/*padding:3px 6px;*/
	margin-right: 20px;
	cursor: pointer;
	height: 24px;
	line-height: 20px
}
.calc-save{
	background-color: #6DB92C;
}
.calc-edit{
	background-color: #457EDA;
}
.reward-ratio>li .ratio-rep{
	display: inline-block;
	padding:0 5px;
	font-size:16px;
	font-weight:bold;
}
.reward-ratio{
	display: inline-block;
	overflow: hidden;
	vertical-align: middle;
}
.reward-ratio>li{
	float:left;
	margin-top:5px;
}
.calc-btn{
	border:none;
	width: 100px;
	height: 40px;
	background-color: #4684E9;
	color:#fff;
	cursor: pointer;
}
.reward-option{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	padding: 10px 0;
}
.form-group{
	padding: 10px 0px;
	display: inline-block;
	margin-right: 40px;
}
.calc-item-form{
	margin-left: 40px;
}
.calc-item-list{
	padding-left: 40px;
}
.calc-subTitle-text{
	display: inline-block;
	position: relative;
	margin-left: 20px;
	width: 100px;
}
.calc-subTitle{
	margin:10px 0;
}
.scale-content{
	background-color:#353B47;
	padding:20px 40px;
}
.monNum-label{
	height: 30px;
	line-height: 30px;
}
.monNum-label>li{
	float: left;
	margin-right: 20px;
}
.monNum-box{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack:start;
	    justify-content:flex-start;
	-ms-flex-wrap:wrap;
	    flex-wrap:wrap;
	width: 1200px;
	margin-left:80px;
	/*margin:0 auto;*/
}
.monNum-box>li{
	text-align: left;
	margin-top:10px;
	width:130px;
}
.monNum-box>li>p{
	height:25px;
	line-height:25px;
}
.monNum-box>li>div{
	margin-top:5px;
}
.monNum-box>li label{
	margin-left: 10px;
}
.radioBtn{
	width: 12px;
	height: 12px;
	display: inline-block;
	border:1px solid #ddd;
	border-radius: 50%;
}
.radioBtn.radioBtnCheck{
	border:3px solid  #4684E8;
	background-color: #fff;
}
.calc-bisc-cont{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	min-width:1200px;
	margin-top: 20px;
}
.calc-bisc-cont>li{
	background-color: #353B47;
	width:32%;
	text-align: center;
	height:90px;
	line-height:90px;
}
.calc-bigInput{
	background-color: transparent;
	height:36px;
	width:160px;
	border:1px solid #797979;
	color: #fff;
	text-align: center;
}
.calc-bigInput>option{
	background-color: #797979;
	border: none;
}
.calc-middleInput{
	background-color: transparent;
	height:36px;
	width:80px;
	border:1px solid #797979;
	color: #fff;
	text-align: center;
}
.calc-smallInput{
	background-color: transparent;
	height:30px;
	width:50px;
	border:1px solid #797979;
	color: #fff;
	background-color: #545A64;
	text-align: center;
}
.calc-item-box{
	margin-top: 20px;
}
.calc-title{
	height: 20px;
	margin:10px 40px;
}
.no-choose{
	display: inline-block;
	width: 18px;
	height: 18px;
	/*border: 1px solid #D84A4A;*/
	background-image: url(../common/img/calc/no-choose.png);
	border-radius: 50%;
	vertical-align: bottom;
}
.no-choose.choosed{
	border:none;
	background-image: url(../common/img/calc/choosed.png);
}
.calc-title-text{
	display: inline-block;
	font-weight: bold;
	font-size: 18px;
	margin-left: 20px;
	padding-left: 10px;
	border-left: 5px solid #fff;
}
.calc-nav{
	height:70px;
	background-color: #457EDA;
	padding:15px 0;
	min-width: 1200px;
}
.batch-label{
	min-width: 500px;
	margin-left:80px;
}
.batch-label>label{
	height: 40px;
	line-height: 40px;
	vertical-align:top;
	display: inline-block;
}
.batch-label>p{
	width: 400px;
	background-color: #5D95F0;
	height: 40px;
	display: inline-block;
}
.batch-label-text{
	width:280px;
	display: inline-block;
	/*line-height: 0px;*/
	white-space: nowrap;
	overflow-x: hidden;
	text-overflow: ellipsis;
}
.batch-label-btn{
	display: inline-block;
	float: right;
	width: 100px;
	height: 40px;
	line-height: 40px;
	text-align: center;
	letter-spacing: 2px;
	background-color: #00CAD2;
}
.batch-msg{
	min-width: 500px;
	text-align: right;
	margin-right: 40px;
	position: relative;
}
.city-list{
	position: absolute;
	top:40px;
	right:10px;
	background-color:#fff;
	width:80px;
	color:#545A64;
	text-align: center;
}
.city-list>li{
	padding: 5px 0;
	cursor: pointer;
	/*border-bottom: 1px solid rgb(103, 107, 115);*/
}
.city-list>li.city-active{
	color:#fff;
	background-color: #457EDA;
}
.batch-msg>p{
	display: inline-block;
	height: 40px;
	line-height: 40px
}
.batch-msg>div{
	display: inline-block;
	height: 34px;
	line-height:34px;
	width: 100px;
	border: 1px solid #fff;
	border-radius:20px;
	text-align: center;
	margin-left: 20px;
	position: relative;
	cursor: pointer;
}





