
.text-over{
	white-space: nowrap;
	overflow-x: hidden;
	text-overflow: ellipsis;
	/*cursor: pointer;*/
}
.check-users-footer{
	text-align:center;
	margin-top:2%;
}
.check-users-confirm{
	width:360px;
	height: 40px;
	background-color:#28be93;
	display: inline-block;
	cursor: default;
	line-height: 40px;
	border-radius:20px;
	font-size: 18px;
	letter-spacing: 2px;
	cursor: pointer;
}
.check-users-close{
	position: absolute;
    top: -20px;
    right: -20px;
    cursor: pointer;
}
.check-users{
	/*width:48%;
	height:80%;*/
	width: 680px;
	height: 580px;
	background-color: #0f376f;
	border-radius: 5px;
	padding: 10px 10px 10px 20px;
	position: fixed;
	top: 50%;
	margin-top: -20%;
	/*margin-left: -340px;*/
}
.check-users-content{
	height:85%;
}
.check-users table{
	width:96%;
	text-align: left;
	margin-left: 20px;
}
.check-users table td:first-child{
	text-align: center;
	width:60px;
}
.check-radio {
	position: relative;
	display: inline-block;
	width: 12px;
	height: 12px;
	border-radius:50%;
}
.command .check-radio {
	background-color: #0e3d6e;
	border:1px solid  #28be93;
}
.command .checked-radio{
	background-color:#fff;
	border:3px solid  #28be93;
}
.check-mine .check-radio{
	background-color:#0e3d6e;
	border:1px solid  #259bdc;
}
.check-mine .checked-radio{
	background-color:#fff;
	border:3px solid #259bdc;
}

